#twcont {
    display: flex;
    justify-content: center;
    align-items: center;

    width: fit-content;
    height: fit-content;
}

#twleft {
    width: fit-content;
    height: fit-content;
}

#twleft #drawcont {
    overflow: hidden;

    width: 450px;
    height: fit-content;

    /* border: 2px solid white; */
    background-color: white;
    border-radius: 3px;

    transition: height .2s;
}
#twleft #drawshowcase {
    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;

    color: black;
    font-size: 18px;
    font-weight: 700;

    width: 100%;
    height: 80px;
}
#twleft #drawplace {
    width: 100%;
    height: 240px;

    padding: 10px 40px;
    padding-bottom: 30px;
}




@media screen and (max-width: 1150px) {
    
    #tttconnect #ccbottomcont {
        padding-top: 150px;
        padding-bottom: 50px;
    }
    #tttconnect #ccbottom {
        /* display: block; */
        flex-direction: column;

        padding: 100px 0px;
        padding-left: 70px;
    }

    #tttconnect #tttinvitecont {
        margin-top: 100px;
    }
    #tttconnect #gamecontrolcont {
        margin-right: 0px;
    }
    
}
