#tttplay {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100%;
}


#board {
    display: flex;
    flex-wrap: wrap;

    width: calc(3 * 180px);
    height: calc(3 * 180px);

    outline: 3px solid white;
}

.cell {
    cursor: pointer;

    position: relative;

    width: 180px;
    height: 180px;

    background-color: black;
    border: 2px solid white;
}
.cell .cellShape {
    position: absolute;
    top: 0px;
    z-index: 20;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100%;

    color: white;
    font-size: 80px;
    font-weight: 900;
}
.cell .cellShape img {
    max-width: 90%;
    max-height: 90%;
}

.cell .cellHover {
    display: flex;
    justify-content: center;
    align-items: center;

    visibility: hidden;

    width: 100%;
    height: 100%;

    color: white;
    font-size: 80px;
    font-weight: 900;

    opacity: .3;
}
.cell .cellHover img {
    max-height: 90%;
    max-width: 90%;
}
.cell .cellHover.hover {
    visibility: visible;
}


#gamestats {
    position: relative;

    display: flex;
    flex-direction: column;
    /* justify-content: space-between;
    align-items: center; */

    width: 300px;
    height: calc(3 * 180px);

    /* background-color: white; */

    margin-left: 40px;
}

#gamestats .player {
    display: flex;
    justify-content: space-between;
    align-items: center;

    width: 100%;
    height: fit-content;

    color: white;
    font-size: 16px;
    font-weight: 600;

    padding: 10px 20px;
    padding-left: 30px;

    /* background-color: white; */
    /* background-color: white; */
    border: 3px solid white;
    border-radius: 5px;

    transition: background-color .2s, border-color .2s, color .2s;
}
#gamestats .player div {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100px;
    height: 50px;

    background-color: black;
    border: 3px solid white;
    border-radius: 7px;

    transition: border-color .2s;
}
#gamestats .player img {
    /* transform: rotate(180deg); */
    height: 30px;
}

#gamestats .player.active {
    color: black;
    background-color: #FFF500;
    border-color: #FFF500;
}
#gamestats .player.active div {
    border-color: black;
}

#gamestats #gsdesc {
    width: 100%;
    height: 100%;

    margin: 15px 0px;

    background-color: black;
    border-radius: 5px;
    border: 3px solid white;
}


#rescont {
    z-index: 200;

    position: fixed;
    top: 0px;
    left: 70px;

    display: none;
    justify-content: center;
    align-items: center;

    width: calc(100% - 70px);
    height: calc(100% - 5px);

    /* background-color: #00000030; */
}

#res {
    /* display: flex;
    justify-content: center;
    align-items: center; */

    width: fit-content;
    height: fit-content;

    background-color: white;
    border-radius: 10px;
    border: 3px solid black;

    padding: 5px 35px;
    padding-bottom: 25px;
}
#res label {
    display: block;
    text-align: center;
}
#res #resres {
    color: black;
    font-size: 150px;
    font-weight: 900;
}
#res button {
    display: block;

    width: 100%;
    height: 50px;

    margin-bottom: 15px;
}






#tttplay #cpresultcont {
    z-index: 301;

    position: fixed;
    top: 0px;
    left: 0px;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 100vw;
    height: 100%;

    background-color: #20202090;
}

#tttplay #cpresultcont #cprescontrol {
    width: 460px;
    height: fit-content;

    background-color: white;
    border-radius: 10px;

    padding: 50px 40px;
}
#tttplay #cpresultcont #cprescontrol label {
    display: block;

    text-align: center;

    color: black;
    font-size: 50px;
    font-weight: 800;

    margin-bottom: 50px;
}
#tttplay #cpresultcont #cprescontrol label span {
    color: #0029FF;
    font-size: 50px;
    font-weight: 800;
}
#tttplay #cpresultcont #cprescontrol button {
    cursor: pointer;

    width: 100%;
    height: 60px;

    color: white;
    font-size: 15px;
    font-weight: 700;

    border: none;
    border-radius: 5px;

    outline: 4px solid transparent;

    transition: outline .1s;
}
#tttplay #cpresultcont #cprescontrol #pabtn {
    background: linear-gradient(97.73deg, #001FC4 0%, #3F5EFD 100%);
}
#tttplay #cpresultcont #cprescontrol #pabtn:hover {
    outline: 4px solid #FFF500;
}
#tttplay #cpresultcont #cprescontrol #gtmbtn {
    background-color: black;
    margin-top: 20px;
}
#tttplay #cpresultcont #cprescontrol #gtmbtn:hover {
    outline: 4px solid #FFF500;
}

